import React from 'react';

import { ContextProvider } from './context/ContextProvider'; // Import JobProvider
import NavBar from './components/NavBar';
import PageBuilder from './components/PageBuilder'
import MindMap from './components/MindMap'
import { BrowserRouter, Routes, Route } from "react-router-dom";

function App() {

  return (
    <BrowserRouter basename="/demos/mindmap">
      
      <ContextProvider>
        <NavBar />
        <Routes>
          <Route path="/" element={<PageBuilder />} />
          <Route path="/map" element={<MindMap />} />
        </Routes>
      </ContextProvider>      
      
    </BrowserRouter>
  );
}

export default App;
