import React, { useEffect, useState } from 'react';
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet';
import { Button } from '@mui/material';
import { useCreatorContext } from '../context/ContextProvider';
import L from 'leaflet';
import {
  Box,
  Typography
} from "@mui/material";
import 'leaflet/dist/leaflet.css';
import { useNavigate } from 'react-router-dom';
const getClusterColor = (clusterId) => {
  // if (clusterId == "-1") {
  //     return "#000000";
  // }
  const colors = [
    "#FF5733", "#33FF57", "#3357FF", "#F833FF", "#FF5733", 
    "#57FF33", "#FF33A1", "#33FFF5", "#FF8C33", "#33FF8C", 
    "#F5FF33", "#8C33FF", "#FF338C", "#33A1FF", "#8CFF33", 
    "#A133FF", "#33FF33", "#8C8CFF", "#FF3388", "#57A1FF"
  ];
  return colors[(clusterId) % colors.length]
};
const MindMap = () => {
  const { view, setView, creator, data, selectSpecificJob } = useCreatorContext();
  const [clusters, setClusters] = useState([]);
  const [creatorId, setCreatorId] = useState("");
  const [item, setItem] = useState(null);
  const navigate = useNavigate();
  useEffect(() => {
    

    if (data) {
      
        const clusterList = Object.keys(data).map((clusterId) => {
            return {
              clusterId,
              items: data[clusterId].items.map((item) => ({
                ...item,
                color: getClusterColor(clusterId),
                clusterId: clusterId,
                topic: data[clusterId].topic
              })),
            };
        });
        setClusters(clusterList);

    }
    
  }, [data]);
  useEffect(() => {
    if (creator) {
      setCreatorId(creator);
    }
  }, [creator]);
  useEffect(() => {
    if (view) {
      setItem(view);
    }
  }, [view]);
  


  const createIcon = (color) => {
    return new L.DivIcon({
      className: 'leaflet-div-icon',
      html: `<span style="color: ${color}; font-size: 24px;">&#10148;</span>`, // Material Icon for location
      iconSize: [30, 30],
      iconAnchor: [15, 30], // Adjust to position the icon correctly on the map
      popupAnchor: [0, -45], // Adjust position of popup relative to icon
    });
  };

  const handlePointClick = (clusterId) => {
    setView("info");
    navigate(`/?source=${creatorId}`);
    selectSpecificJob(data, clusterId);
  };

  if (!clusters || clusters.length == 0) {
    return <p>Loading...</p>;
  }

  return (
    
    <Box sx={{ padding: 3 }}>
    <Typography variant="h5" gutterBottom>
        Mind Map
    </Typography>
    
    <div style={{ height: '500px', width: '100%' }}>
      <MapContainer center={[0, 0]} zoom={5} style={{ height: '100%', width: '100%' }}>
        {/* Blank Tile Layer */}
        {/* <TileLayer
          url="https://coolbackgrounds.io/images/unsplash/samuel-zeller-medium-b832fe04.jpg"  // Blank tile (or any custom blank tile)
        /> */}
        <TileLayer
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          noWrap={true}
          opacity={0}  // Make the tile layer invisible
        />
        

        {clusters.map((cluster, clusterIndex) =>
              cluster.items.map((item, index) => (
                <Marker key={index} position={[item.location[1], item.location[0]]} icon={createIcon(item.color)}>
                  <Popup>
                  <div>
                    
                    <Typography variant="body1" gutterBottom>
                      Video: {item.llm_title}
                    </Typography>
                    <Button 
                      variant="contained" 
                      color="primary" 
                      onClick={() => handlePointClick(item.clusterId)} // Handle button click event
                    >
                      See topic: {item.topic}
                    </Button>
                  </div>
                </Popup>
                </Marker>

              ))
        )}


      </MapContainer>
      </div>
      </Box>
    
  );
};

export default MindMap;