import React, { useEffect, useState } from "react";
import {
  Container,
  Typography,
  Card,
  CardContent,
  Grid,
  Link,
  CardActionArea,
  CardMedia,
} from "@mui/material";
import { useCreatorContext } from "../context/ContextProvider";

const GroupSummary = () => {
  const { groupDetails } = useCreatorContext();
  const [data, setData] = useState(null);

  useEffect(() => {
    if (groupDetails) {
      setData(groupDetails);
    }
  }, [groupDetails]);

  if (!data) {
    return <p>Loading ...</p>;
  }

  const createImageLink = (id) => {
    if (!id) {
      throw new Error("ID is required to generate an image link.");
    }
  
    // Assuming a standard format for image links
    const baseURL = "https://eric-yim.github.io/demos/thumbs"; // Replace with your actual base URL
    return `${baseURL}/${id}.png`; // Adjust extension if needed (e.g., .png, .webp)
  };

  const ifNoneSayUnsorted = (word) => {
    if (word == "NA") {
        return "Ungrouped Videos"
    }
    return word;
  }

  const sortedItems = data.items.sort((a, b) => b.idx - a.idx);

  return (
    <Container sx={{ mt: 4 }}>
      {/* Topic */}
      <Typography variant="h6" gutterBottom>
        <strong>Topic:</strong> {ifNoneSayUnsorted(data.topic)}
      </Typography>

      {/* Advice */}
      <Typography variant="h6" gutterBottom>
        <strong>Main Message:</strong> {data.advice}
      </Typography>

      {/* Items */}
      <Grid container spacing={4}>
        {sortedItems.map((item) => (
          <Grid item xs={12} sm={6} md={4} key={item.idx}>
            <Card>
              <CardActionArea component={Link} href={item.link} target="_blank">
                {/* Image */}
                
                <CardContent>
                  {/* Title */}
                  <Typography variant="body1" gutterBottom>
                    {item.llm_title}
                  </Typography>
                  <div style={{ display: 'flex', justifyContent: 'center' }}>
                    <CardMedia
                    component="img"
                    alt={`Image for ${item.llm_title}`}
                    sx={{
                        width: "auto", // Ensures the image takes the full width of the card
                        height: "128px", // Fixed height for the image
                    }}
                    image={createImageLink(item.idx)} // Generate image link using the idx
                    />
                </div>
                </CardContent>
              </CardActionArea>
            </Card>
          </Grid>
        ))}
      </Grid>
    </Container>
  );
};

export default GroupSummary;
