import React, { useEffect, useState } from "react";
import AppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import { useNavigate } from 'react-router-dom';
import { useCreatorContext } from '../context/ContextProvider'; // Import JobProvider

const NavBar = () => {

  const navigate = useNavigate();
  const { view, setView, data, creator, selectRandomJob } = useCreatorContext();
  const [item, setItem] = useState(null);
  const [creatorId, setCreatorId] = useState("");
  // Function to handle random selection on button click
  const handleRandomSelection = () => {
    if (data) {
      setView("info");
      navigate(`/?source=${creator}`);
      selectRandomJob(data); 
    }
  };

  useEffect(() => {
    if (view) {
      setItem(view);
    }
  }, [view]);

  useEffect(() => {
    if (creator) {
      setCreatorId(creator);
    }
  }, [creator]);

  const handleMapView = () => {
    if (item == "map") {
      setView("info");
      navigate(`/?source=${creator}`);
      
      return;
    }
    setView("map");
    navigate(`/map?source=${creatorId}`);
    
  };

  const checkView = () => {
    if (item == "map") {
      return "Info View";
    }
    return "Map View";
  }

  return (
    <AppBar position="sticky" sx={{ backgroundColor: "#333" }}>
      <Toolbar>
        {/* Navbar Title */}
        <Typography variant="h6" sx={{ flexGrow: 1 }}>
          Hello World!
        </Typography>

        {/* Shuffle Button */}
        <Button
          variant="contained"
          color="primary"
          onClick={handleRandomSelection} 
          sx={{
            marginLeft: "auto", 
          }}
        >
          Shuffle Topic
        </Button>
        <Button
          variant="contained"
          color="secondary"
          onClick={handleMapView} 
          sx={{
            marginLeft: "10px", 
          }}
        >
          {checkView()}
          
        </Button>
      </Toolbar>
    </AppBar>
  );
};

export default NavBar;